<template>
  <Lay :route="route" :arr="arr" :url="url"/>
</template>

<script>
  import Lay from '@/components/common/template';
  import {completiongear} from '@/config/wellcompletion'
  export default {
    props: ["route"],
    data () {
      return {
        url: '/data/query/completionwellheaddevice',    
        arr: completiongear
      }
    },

    components: {
      Lay
    },

    mounted: function() {
    
    },

    methods: {
    }
  }
</script>



